import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Formulario_indicador } from '../../../modelo/Formulario_indicador'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'formulario_indicador-dados-details',   templateUrl: 'formulario_indicador_dadosDetails.component.html' }) export class Formulario_indicadorDadosDetailsComponent implements OnInit {    formulario_indicador: Formulario_indicador;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.formulario_indicador.Formulario_indicador";                 let id = +params['id'];         this.formulario_indicador = new Formulario_indicador();         this.formulario_indicador.cd_formulario_indicador = id;         o.objetoJson = this.formulario_indicador;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a formulario_indicador. '; }      this.formulario_indicador = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


